::-webkit-scrollbar {
    width: 10px;
}

::-webkit-scrollbar-track {
    background: #2f2f37;
}

::-webkit-scrollbar-thumb {
    background: #8585ad;
}

::-webkit-scrollbar-thumb:hover {
    background: #8585ad;
}

#root{
    display: flex;
    flex-direction: column;
    height: 100vh;
}
#modal{
    position: relative;
    z-index: 1000000;
}