.modal_window {
    background-color: #1c1c21;
    border-radius: 10%;
    margin: 15% auto;
}

.modal_content {
    padding: 40px;
}

.close {
    float: right;
    padding: 40px;
}

.close:hover,
.close:focus {
    color: black;
    text-decoration: none;
    cursor: pointer;
}